.section-title {
  margin: 0;
  margin-bottom: 48px;

  font-weight: 700;
  font-size: 32px;
  line-height: 100%;
  letter-spacing: -1.5px;

  @include on-tablet {
    margin-bottom: 64px;
    font-size: 40px;
  }

  @include on-desktop {
    font-size: 48px;
  }

  &__title--align--right {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
  }
}
